/* Radio */
.radio {
  display: inline-flex;
  align-items: center;
  margin-bottom: 10px;
  /* Checked */
  /* Focused */
  /* Disabled */
  /* Checked disabled */
}

.radio:not(:last-child) {
  margin-right: 10px;
}

.radio__text {
  user-select: none;
  cursor: pointer;
}

.radio__box {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid transparent;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: border 0.3s ease;
}

.radio__box::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
  border-radius: 50%;
  background-color: rgba(136, 136, 136, 0.493);
  transform: translate(-50%, -50%);
  transition: opacity 0.2s ease;
}

.radio__box--rhombus {
  margin-right: 10px;
  background: url(../img/rhombus.jpg) center/115% 115% no-repeat;
}

.radio__box--honeycomb {
  margin-right: 10px;
  background: url(../img/honeycomb.jpg) center/115% 115% no-repeat;
}

.radio__box--black {
  background-color: black;
}

.radio__box--grey {
  background-color: grey;
}

.radio__box--red {
  background-color: red;
}

.radio__box--blue {
  background-color: blue;
}

.radio__box--green {
  background-color: green;
}

.radio__box--yellow {
  background-color: yellow;
}

.radio__box--purple {
  background-color: purple;
}

.radio__box--pink {
  background-color: pink;
}

.radio__box--brown {
  background-color: brown;
}

.radio__box--white {
  background-color: white;
}

.radio__box--light-blue {
  background-color: aqua;
}

@media (max-width: 479.98px) {
  .radio__box {
    width: 28px;
    height: 28px;
  }
}

.radio__input:checked + .radio__box {
  border: 2px solid #70d03a;
}

.radio__input:focus + .radio__box {
  border: 4px solid #70d03a;
}

.radio__input:disabled + .radio__box {
  border: 2px solid #9b9b9b;
}

.radio__input:disabled + .radio__box::before {
  opacity: 1;
  transition: opacity 0.3s ease;
}

.radio__input:disabled ~ * {
  cursor: default;
}

.radio__input:checked:disabled + .radio__box::before {
  background-color: #9b9b9b;
  transition: opacity 0.2s ease;
}

/* Check */
.check {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Checked */
  /* Focused */
  /* Disabled */
  /*Checked disabled */
}

.check__text {
  user-select: none;
  cursor: pointer;
}

.check__box {
  position: relative;
  display: inline-block;
  width: 65px;
  height: 33px;
  border: 4px solid transparent;
  border-radius: 50px;
  background-color: #c4c4c4;
  transition: background-color 0.3s ease, border 0.2s ease-in-out;
}

.check__box::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: #fff;
  transform: translate(0px, -50%);
  transition: transform 0.3s ease;
}

@media (max-width: 767.98px) {
  .check__box {
    width: 48px;
    height: 25px;
  }
  .check__box::after {
    width: 21px;
    height: 21px;
  }
}

.check__input:checked + .check__box {
  background-color: #00ab30;
  transition: background-color 0.3s ease, border 0.3s ease;
}

.check__input:checked + .check__box::after {
  transform: translate(120%, -50%);
  transition: transform 0.3s ease;
}

@media (max-width: 767.98px) {
